@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Miss+Fajardose&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Parisienne&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;

  background: linear-gradient(
      rgba(228, 228, 228, 0.8),
      rgba(228, 228, 228, 0.8)
    ),
    url(/wedding-countdown/static/media/belair.c91d84e6.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "Playfair Display", serif;
}
.top {
  margin-top: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 450px;
}
.top_save-the-date {
  font-family: "Parisienne", cursive;
  font-size: 50px;
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
}
.top_for {
  font-size: 20px;
  text-align: center;
  margin-bottom: 20px;
}
.top_name-holder {
  font-size: 30px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.top_name-first {
  display: -webkit-flex;
  display: flex;
  text-align: left;

}
.and_sign {
  font-family: "Parisienne", cursive;
}
.top_name-second {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.name {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right: 15px;
  font-size: 40px;
  font-weight: bold;
}
.surname {
  font-family: "Miss Fajardose", cursive;
  font-size: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 25px;
}
.bottom__info {
  width: 280px;
  margin-top: 40px;
}
.bottom__date {
  text-align: center;
  font-size: 30px;
  text-align: right;
}
.bottom__location {
  margin-top: 15px;
  font-family: "Parisienne", cursive;
  text-align: right;
  font-size: 25px;
}
